@import '../mixins';

.split-img-text {
    padding: 120px 125px;
    @include respond(sm-tab-port){
        padding: 96px 16px;
    }
    &__body {
        &__heading {
            color: #141414;
            font-family: Inter Tight;
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.4px;
            margin-bottom: 32px;
        }

        &__paragraph {
            color: var(--Wivians-Factory-Gray, #3F3F3E);

            /* Desktop/Body */
            font-family: Inter Tight;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: 30px;
            /* 125% */
            letter-spacing: 0.48px;
            margin-bottom: 44px;
        }
    }
    @include respond(phone){
        &__body {
            &__heading {
                font-size: 16px;

            }
    
            &__paragraph {
                font-size: 18px;
            }
        }
    }
}